.row{
    color:white;
    margin-left:20px;
}

.row-posters{
    display:flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding:12px;
}
.row-posters::-webkit-scrollbar{
    display:none;
}

.row-poster{
    max-height:100px;
    object-fit:contain;
    width:100%;
    transition:transform 450ms;
}

.row-poster:hover{
    transform:scale(1.08);
    opacity:1;
}

.row-poster-large{
    max-height: 250px;
}

.row-poster-large:hover{
    transform:scale(1.1);
    opacity:1;
}